@font-face {
    font-family: 'FS Albert Pro';
    src: url('FSAlbertPro-Bold.eot');
    src: local('FS Albert Pro Bold'), local('FSAlbertPro-Bold'),
        url('FSAlbertPro-Bold.eot?#iefix') format('embedded-opentype'),
        url('FSAlbertPro-Bold.woff2') format('woff2'),
        url('FSAlbertPro-Bold.woff') format('woff'),
        url('FSAlbertPro-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

